<template>
  <section class="main">
    <div class="container main-container left-main">
      <div class="left-container">
        <div class="tweet">
          <div class="pin-header-row">
            <div class="account-group">
              <div>
                <a
                  :href="'/user/' + tweet.user.id"
                  :title="tweet.user.nickname"
                >
                  <img :src="tweet.user.smallAvatar" class="avatar size-30" />
                </a>
              </div>
              <div class="pin-header-content">
                <div>
                  <a
                    :href="'/user/' + tweet.user.id"
                    :title="tweet.user.nickname"
                    target="_blank"
                    class="nickname"
                    >{{ tweet.user.nickname }}</a
                  >
                  <span class="dot">·</span>
                  <span class="position ellipsis">
                    {{ tweet.user.description }}
                  </span>
                </div>
                <div class="meta-box">
                  <time
                    :datetime="
                      tweet.createTime | formatDate('yyyy-MM-ddTHH:mm:ss')
                    "
                    itemprop="datePublished"
                    >{{ tweet.createTime | prettyDate }}</time
                  >

                  <span class="meta-item">
                    <!-- <span
                      v-for="tag in topic.tags"
                      :key="tag.tagId"
                      class="tag"
                    >
                      <a :href="'/topics/tag/' + tag.tagId">{{
                        tag.tagName
                      }}</a>
                    </span> -->
                  </span>
                  <span v-if="hasPermission" class="operate">
                    <a @click="deleteTweet(tweet.tweetId)">
                      <i class="iconfont icon-delete" />删除
                    </a>
                  </span>
                  <!-- <span class="meta-item act">
                    <a>
                      <i class="iconfont icon-favorite" />&nbsp;{{
                        true ? '已收藏' : '收藏'
                      }}
                    </a>
                  </span> -->
                  <!-- <span class="meta-item act">
                    <a
                      v-clipboard:error=""
                      v-clipboard:copy=""
                      v-clipboard:success=""
                    >
                      &nbsp;&nbsp;&nbsp;&nbsp;
                      <i class="iconfont icon-share" />分享
                    </a>
                  </span> -->
                </div>
              </div>
            </div>
          </div>
          <div class="pin-content-row">
            <div class="content-box">{{ tweet.content }}</div>
          </div>
          <ul
            v-if="tweet.imageList && tweet.imageList.length > 0"
            v-viewer
            class="pin-image-row"
          >
            <li v-for="(image, idx) in tweet.imageList" :key="idx">
              <div class="image-item">
                <img :src="image.preview" :data-src="image.url" />
              </div>
            </li>
          </ul>
          <div class="pin-action-row">
            <div class="action-box">
              <div class="like-action action" @click="like(tweet)">
                <div class="action-title-box">
                  <i class="iconfont icon-like" />
                  <span class="action-title">{{
                    tweet.likeCount > 0 ? tweet.likeCount : '赞'
                  }}</span>
                </div>
              </div>
              <div class="comment-action action">
                <div class="action-title-box">
                  <i class="iconfont icon-comments" />
                  <span class="action-title">{{
                    tweet.commentCount > 0 ? tweet.commentCount : '评论'
                  }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <comment
          :entity-id="tweet.tweetId"
          :comments-page="commentsPage"
          mode="text"
          entity-type="tweet"
        />
      </div>
      <div class="right-container">
        <site-notice />
        <score-rank :score-rank="scoreRank" />
      </div>
    </div>
  </section>
</template>

<script>
import Vue from 'vue'
import Viewer from 'v-viewer'
import SiteNotice from '~/components/SiteNotice'
import ScoreRank from '~/components/ScoreRank'
import Comment from '~/components/Comment'
import utils from '~/common/utils'
import UserHelper from '~/common/UserHelper'
import 'viewerjs/dist/viewer.css'

Vue.use(Viewer, {
  defaultOptions: {
    zIndex: 9999,
    navbar: false,
    title: false,
    tooltip: false,
    movable: false,
    scalable: false,
    url: 'data-src'
  }
})

export default {
  components: {
    SiteNotice,
    ScoreRank,
    Comment
  },
  async asyncData({ $axios, params }) {
    const [tweet, commentsPage, scoreRank] = await Promise.all([
      $axios.get('/api/tweet/' + params.id),
      $axios.get('/api/comment/list', {
        params: {
          entityType: 'tweet',
          entityId: params.id
        }
      }),
      $axios.get('/api/user/score/rank')
    ])
    return { tweet, commentsPage, scoreRank }
  },
  computed: {
    hasPermission() {
      return (
        this.isOwner ||
        UserHelper.isOwner(this.user) ||
        UserHelper.isAdmin(this.user)
      )
    },
    isOwner() {
      if (!this.user || !this.topic) {
        return false
      }
      return this.user.id === this.topic.user.id
    },
    user() {
      return this.$store.state.user.current
    }
  },
  methods: {
    async deleteTweet(tweetId) {
      if (process.client && !window.confirm('是否确认删除该动态？')) {
        return
      }
      try {
        await this.$axios.post('/api/tweet/delete/' + tweetId)
        this.$toast.success('删除成功', {
          duration: 1000,
          onComplete() {
            utils.linkTo('/tweets')
          }
        })
      } catch (e) {
        console.error(e)
        this.$toast.error('删除失败：' + (e.message || e))
      }
    },
    async like(tweet) {
      try {
        await this.$axios.post('/api/tweet/like/' + tweet.tweetId)
        tweet.liked = true
        tweet.likeCount++
      } catch (e) {
        if (e.errorCode === 1) {
          this.$toast.info('请登录后点赞！！！', {
            action: {
              text: '去登录',
              onClick: (e, toastObject) => {
                utils.toSignin()
              }
            }
          })
        } else {
          this.$toast.error(e.message || e)
        }
      }
    }
  },
  head() {
    return {
      title: this.$siteTitle('90社区—有趣动态'),
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: this.$siteDescription()
        },
        { hid: 'keywords', name: 'keywords', content: this.$siteKeywords() }
      ]
    }
  }
}
</script>

